.popup__container {
    width: 430px;
    /* min-height: 330px; */
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 780px) {
    .popup__container {
        min-width: 282px;
        /* min-height: 322px; */
    }
}